import React, { useContext, useCallback, useMemo, useState } from 'react';
import PropTypes from 'prop-types';
import { TypeaheadMultiSelectField } from '@jutro/components';
import { set, omit, pickBy, isObject } from 'lodash';
import { TranslatorContext } from '@jutro/locale';
import { intlMessageShape } from '@jutro/prop-types';
import { Grid } from '@jutro/layout';
import {
    useSetComponentValue,
    useSetDefaultCountry,
    useSetComponentValidation,
    useSetValueForUnknownCountry,
} from './AddressComponents.hooks';
import { AddressForm } from './addressForm/AddressForm';
import { labelMessages } from './AddressComponent.messages';
import { countriesAddressData, defaultCountryForm } from './defaultCountryData';

export const AddressComponent = ({
    value,
    onValueChange,
    onValidationChange,
    countriesData,
    defaultCountry,
    readOnly,
    readOnlyFormat,
    readOnlyLabel,
    showCountrySelection = 'selectable',
    id,
    path,
    showErrors,
    alwaysShowMaskPostalCode,
}) => {
    const translator = useContext(TranslatorContext);
    const [componentValue, setComponentValue] = useState(value);
    const [isComponentValid, setIsComponentValid] = useState(true);

    const countriesDataMerged = useMemo(() => {
        const data = countriesData
            ? { ...countriesData }
            : { ...countriesAddressData };
        return pickBy(data, isObject);
    }, [countriesData]);

    const resolveCountriesForDropdown = useMemo(() => {
        const availableCountries = Object.keys(countriesDataMerged)?.map(
            country => {
                const countryCode = countriesDataMerged[country].countryCode;

                return {
                    code: countryCode,
                    name: translator(countriesDataMerged[countryCode]?.country),
                };
            }
        );

        if (
            availableCountries.every(
                country => country?.code !== value?.country
            )
        ) {
            const unknownCountryDropdownOption = {
                code: value?.country,
                name: value?.country,
            };
            availableCountries.push(unknownCountryDropdownOption);
        }

        const countriesDropdownAlphabeticalOrder = availableCountries
            .map(country => country.name)
            .sort();

        return countriesDropdownAlphabeticalOrder.map(
            country =>
                availableCountries.filter(({ name }) => name === country)[0]
        );

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useSetComponentValidation(isComponentValid, onValidationChange);

    useSetComponentValue(componentValue, onValueChange, path);

    useSetDefaultCountry(
        defaultCountry,
        countriesDataMerged,
        resolveCountriesForDropdown,
        setComponentValue
    );

    useSetValueForUnknownCountry(
        value,
        countriesDataMerged,
        defaultCountryForm,
        setComponentValue
    );

    const validateAddressComponent = useCallback(isFormValid => {
        setIsComponentValid(isFormValid);
    }, []);

    const handleWrite = useCallback((fieldValue, fieldPath) => {
        setComponentValue(prevState => {
            let newState = { ...prevState };
            fieldPath.endsWith('postalCode')
                ? set(newState, fieldPath, fieldValue.toUpperCase())
                : set(newState, fieldPath, fieldValue);
            if (!fieldValue) {
                newState = omit(newState, [fieldPath]);
            }
            return newState;
        });
    }, []);

    const handleCountryDropdownWrite = useCallback(dropdownValue => {
        setComponentValue(prevState => {
            if (prevState?.country !== dropdownValue?.code) {
                return { country: dropdownValue?.code };
            }
            return prevState;
        });
    }, []);

    const countryValueWithCountryName = {
        code: componentValue?.country,
        name:
            translator(countriesDataMerged[componentValue?.country]?.country) ||
            componentValue?.country,
    };

    const resolveCountryToRender = useMemo(() => {
        const valueCountryCode = componentValue?.country;
        const countryFromValue = countriesDataMerged?.[valueCountryCode];

        if (!countryFromValue) {
            return defaultCountryForm;
        }

        return countryFromValue;
    }, [countriesDataMerged, componentValue?.country]);

    if (readOnly) {
        // resolve country name for readonly
        const readOnlyValue = { ...componentValue };
        readOnlyValue.country = countryValueWithCountryName;

        return (
            <AddressForm
                value={readOnlyValue}
                countryData={resolveCountryToRender}
                readOnly={readOnly}
                readOnlyFormat={readOnlyFormat}
                readOnlyLabel={readOnlyLabel}
                id={id}
            />
        );
    }

    if (showCountrySelection === 'hidden') {
        return (
            <AddressForm
                value={componentValue}
                onValueChange={handleWrite}
                countryData={resolveCountryToRender}
                id={id}
                onValidationChange={validateAddressComponent}
                showErrors={showErrors}
            />
        );
    }

    return (
        <Grid hgap="large" vgap="medium">
            <TypeaheadMultiSelectField
                id={`${id}_countrySelect`}
                path="country"
                label={translator(labelMessages.countryLabel)}
                availableValues={resolveCountriesForDropdown}
                onValueChange={handleCountryDropdownWrite}
                value={countryValueWithCountryName}
                singleSelect
                dataType="object"
                readOnly={showCountrySelection === 'readOnly'}
                onValidationChange={validateAddressComponent}
                showErrors={showErrors}
            />
            <AddressForm
                value={componentValue}
                onValueChange={handleWrite}
                countryData={resolveCountryToRender}
                metadataFormKey={componentValue?.country}
                id={id}
                onValidationChange={validateAddressComponent}
                showErrors={showErrors}
                alwaysShowMaskPostalCode={alwaysShowMaskPostalCode}
            />
        </Grid>
    );
};

AddressComponent.propTypes = {
    value: PropTypes.object,
    onValueChange: PropTypes.func,
    onValidationChange: PropTypes.func,
    countriesData: PropTypes.object,
    defaultCountry: PropTypes.string,
    readOnly: PropTypes.bool,
    readOnlyFormat: PropTypes.oneOf([
        'oneLine',
        'envelopeBigToSmall',
        'envelopeSmallToBig',
    ]),
    readOnlyLabel: intlMessageShape,
    showCountrySelection: PropTypes.oneOf(['selectable', 'readOnly', 'hidden']),
    id: PropTypes.string.isRequired,
    path: PropTypes.string,
    showErrors: PropTypes.bool,
    /**
     * If true, the mask for postal code will be visible if it is contained in the config, if false postal code will display the placeholder if not focused and empty.
     */
    alwaysShowMaskPostalCode: PropTypes.bool,
};
