import React, { useMemo, useContext } from 'react';
import PropTypes from 'prop-types';
import { isEmpty } from 'lodash';
import { MetadataForm } from '@jutro/uiconfig';
import { TranslatorContext } from '@jutro/locale';
import { intlMessageShape } from '@jutro/prop-types';
import { GridLayout, GridItem } from '@jutro/layout';
import { AddressReadonly } from '../addressReadOnly/AddressReadonly';
import { labelMessages } from '../AddressComponent.messages';

const defaultLayoutTypes = {
    type1: {
        columns: ['84px', 'auto'],
        colspan: 2,
        affectedFieldsName: ['postalCode', 'city'],
    },
    type2: {
        columns: ['auto', '84px'],
        colspan: 2,
        affectedFieldsName: ['city', 'postalCode'],
    },
    type3: {
        columns: ['auto', '128px', '84px'],
        colspan: 3,
        affectedFieldsName: [
            'city',
            'state',
            'province',
            'department',
            'oblast',
            'postalCode',
        ],
    },
    type4: {
        columns: ['84px', 'auto'],
        colspan: 2,
        affectedFieldsName: ['postalCode'],
    },
    type5: {
        columns: ['84px', 'auto'],
        colspan: 2,
        affectedFieldsName: ['postalCode'],
    },
    type6: {
        columns: ['auto', '84px', '128px'],
        colspan: 3,
        affectedFieldsName: ['city', 'state', 'postalCode'],
    },
};

export const AddressForm = ({
    value,
    onValueChange,
    countryData,
    uiProps,
    showErrors,
    readOnly,
    readOnlyFormat,
    readOnlyLabel,
    metadataFormKey,
    id,
    customLayoutTypes,
    onValidationChange,
    alwaysShowMaskPostalCode = true,
}) => {
    const translator = useContext(TranslatorContext);
    const layoutTypes = customLayoutTypes
        ? { ...defaultLayoutTypes, ...customLayoutTypes }
        : defaultLayoutTypes;
    const layoutType = countryData?.fieldsLayoutType
        ? layoutTypes[countryData.fieldsLayoutType]
        : undefined;
    const columnsLayout = {
        columns: layoutType ? layoutType.columns : ['1fr'],
        colspan: layoutType ? layoutType.colspan : 1,
        affectedFieldsName: layoutType ? layoutType.affectedFieldsName : [],
    };
    const componentUiProps = useMemo(() => {
        if (countryData && isEmpty(uiProps)) {
            const newUiProps = {};

            newUiProps.content = countryData.fields.map(field => {
                const {
                    name: fieldName,
                    path: fieldPath,
                    values: fieldValues,
                    pattern: fieldPattern,
                    maxLength: fieldMaxLength,
                    label: fieldLabel,
                    mask: fieldMask,
                    tooltip: fieldTooltip,
                } = field;

                const translatedFieldValues = fieldValues?.map(fieldValue => {
                    return {
                        code: fieldValue.code,
                        name: translator(fieldValue.name),
                    };
                });

                const isRequired = countryData.requiredFields?.some(
                    requiredField => requiredField === fieldName
                );
                const colspan = columnsLayout.affectedFieldsName.includes(
                    fieldName
                )
                    ? 1
                    : columnsLayout.colspan;

                const component = () => {
                    if (fieldValues) {
                        return 'DropdownSelect';
                    }
                    // InputMask usage should be uncommented after finishing current epic with address theming
                    // and jutro peer dependencies should be updated to ^6.2.0 and then new package published
                    // because even proper postal code gives wrong validation before fix on 6.2.0
                    // validation is correct if you type right postal code and delete one char
                    // as stated in the bug descritipion - validation for input mask is returned for one value back
                    // see JUT-8079

                    // if (fieldName === 'postalCode' && fieldMask) {
                    //     return 'InputMask';
                    // }
                    return 'Input';
                };

                const fieldMetadata = {
                    id: `${fieldName}_wrapper`,
                    type: 'element',
                    component: GridItem,
                    componentProps: {
                        colSpan: colspan,
                        phone: { colSpan: 1 },
                        phoneWide: { colSpan: colspan },
                        tablet: { colSpan: colspan },
                    },
                    content: [
                        {
                            id: `${id}_${fieldName}`,
                            type: 'field',
                            component: component(),
                            componentProps: {
                                path: fieldPath,
                                label: translator(
                                    fieldLabel || labelMessages[fieldName]
                                ),
                                availableValues: translatedFieldValues,
                                required: isRequired,
                                labelClassName: 'addressField',
                                dataType: fieldValues ? 'object' : undefined,
                                mask: fieldMask,
                                tooltip: fieldTooltip,
                                alwaysShowMask: alwaysShowMaskPostalCode,
                                maskChar: '#',
                            },
                        },
                    ],
                };

                if (fieldPattern) {
                    fieldMetadata.content[0].componentProps.validator = {
                        pattern: fieldPattern,
                        message: translator(
                            labelMessages.patternValidationError
                        ),
                    };
                }

                if (!fieldValues) {
                    fieldMetadata.content[0].componentProps.maxLength =
                        fieldMaxLength || 60;
                }

                return fieldMetadata;
            });

            return newUiProps;
        }

        return uiProps;
    }, [
        countryData,
        uiProps,
        columnsLayout.affectedFieldsName,
        columnsLayout.colspan,
        id,
        translator,
        alwaysShowMaskPostalCode,
    ]);

    const resolveSmallToBigValue = useMemo(() => {
        const fieldsOrderForCountry = countryData?.fields || [];

        const valueForReadOnly = fieldsOrderForCountry.reduce(
            (sortedValue, field) => sortedValue.concat(value?.[field.name]),
            []
        );

        valueForReadOnly.push(value?.country);

        return valueForReadOnly.filter(Boolean);
    }, [countryData, value]);

    if (readOnly) {
        return (
            <AddressReadonly
                address={resolveSmallToBigValue}
                format={readOnlyFormat}
                label={readOnlyLabel}
                id={id}
            />
        );
    }

    return (
        <GridLayout
            id="addressLayout"
            columns={columnsLayout.columns}
            hgap="large"
            vgap="medium"
            phone={{ columns: [1] }}
            phoneWide={{ columns: columnsLayout.columns }}
            tablet={{ columns: columnsLayout.columns }}
        >
            <MetadataForm
                uiProps={componentUiProps}
                data={value}
                onDataChange={onValueChange}
                showRequired
                showErrors={showErrors}
                key={metadataFormKey}
                onValidationChange={onValidationChange}
                id="addressMetadataForm"
            />
        </GridLayout>
    );
};

export const layoutTypesShape = PropTypes.shape({
    columns: PropTypes.arrayOf(PropTypes.string.isRequired).isRequired,
    colspan: PropTypes.number.isRequired,
    affectedFieldsName: PropTypes.arrayOf(PropTypes.string.isRequired)
        .isRequired,
});

AddressForm.propTypes = {
    value: PropTypes.object,
    onValueChange: PropTypes.func,
    onValidationChange: PropTypes.func,
    countryData: PropTypes.object,
    uiProps: PropTypes.object,
    showErrors: PropTypes.bool,
    readOnly: PropTypes.bool,
    readOnlyFormat: PropTypes.oneOf([
        'oneLine',
        'envelopeBigToSmall',
        'envelopeSmallToBig',
    ]),
    readOnlyLabel: intlMessageShape,
    metadataFormKey: PropTypes.string,
    id: PropTypes.string.isRequired,
    customLayoutTypes: PropTypes.objectOf(layoutTypesShape.isRequired),
    alwaysShowMaskPostalCode: PropTypes.bool,
};
