import { useEffect } from 'react';
import { omit, isEqual } from 'lodash';
import { getDefaultCountryCode } from '@jutro/locale';

export const useSetDefaultCountry = (
    defaultCountry,
    countriesDataMerged,
    countriesForDropdown,
    setComponentValue
) => {
    useEffect(() => {
        const defaultCountryCode =
            defaultCountry || getDefaultCountryCode() || 'US';

        const countryToSetAsDefault = countriesForDropdown.filter(
            country => country.code === defaultCountryCode
        );

        const countryToRenderAsDefault = countryToSetAsDefault.length
            ? countryToSetAsDefault[0].code
            : countriesForDropdown[0].code;

        setComponentValue(prevValue => {
            if (!prevValue?.country) {
                return {
                    country: countryToRenderAsDefault,
                };
            }
            return prevValue;
        });
    }, [
        defaultCountry,
        countriesDataMerged,
        setComponentValue,
        countriesForDropdown,
    ]);
};

export const useSetComponentValue = (componentValue, onValueChange, path) => {
    useEffect(() => {
        onValueChange && onValueChange(componentValue, path);
    }, [componentValue, onValueChange, path]);
};

export const useSetComponentValidation = (
    isComponentValid,
    onValidationChange
) => {
    useEffect(() => {
        onValidationChange && onValidationChange(isComponentValid);
    }, [isComponentValid, onValidationChange]);
};

export const useSetValueForUnknownCountry = (
    value,
    countriesDataMerged,
    defaultCountryForm,
    setComponentValue
) => {
    useEffect(() => {
        if (
            value &&
            !Object.keys(countriesDataMerged).includes(value.country)
        ) {
            const defultFormFields = defaultCountryForm.fields.map(
                field => field?.name
            );
            const valueFromUknownFields = [];
            const fieldsToOmitFromTheValue = [];
            Object.keys(value)
                .filter(fieldName => fieldName !== 'country')
                .forEach(field => {
                    if (!defultFormFields.includes(field)) {
                        valueFromUknownFields.push(
                            value[field].name || value[field]
                        );
                        fieldsToOmitFromTheValue.push(field);
                    }
                });

            if (valueFromUknownFields.length) {
                const firstEmptyAddressLine = value.addressLine3
                    ? 'addressLine3'
                    : 'addressLine2';

                setComponentValue(prevValue => {
                    const newValue = omit(prevValue, fieldsToOmitFromTheValue);
                    if (prevValue[firstEmptyAddressLine]) {
                        newValue[firstEmptyAddressLine] = `${
                            prevValue[firstEmptyAddressLine]
                        }, ${valueFromUknownFields.join(', ')}`;
                    } else {
                        newValue[
                            firstEmptyAddressLine
                        ] = valueFromUknownFields.join(', ');
                    }

                    return isEqual(prevValue, newValue) ? prevValue : newValue;
                });
            }
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
};
