import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { TranslatorContext } from '@jutro/locale';
import { intlMessageShape } from '@jutro/prop-types';
import { InputField } from '@jutro/components';
import css from './AddressReadonly.module.scss';

const mapAddressEntry = addressEntry =>
    typeof addressEntry === 'string' ? addressEntry : addressEntry.name;

const getAddressLine = (format, address) => {
    if (address) {
        const addressValues = address.map(mapAddressEntry);
        switch (format) {
            case 'envelopeSmallToBig': {
                return addressValues.join(', \n');
            }
            case 'envelopeBigToSmall': {
                return addressValues.reverse().join(', \n');
            }
            case 'oneLine': {
                return addressValues.join(', ');
            }
            default: {
                throw new Error('Wrong format');
            }
        }
    }
};

export const AddressReadonly = ({ id, label, format = 'oneLine', address }) => {
    const translator = useContext(TranslatorContext);

    return (
        <InputField
            label={translator(label)}
            readOnly
            className={format !== 'oneLine' ? css.addressReadonly : null}
            value={getAddressLine(format, address)}
            id={id}
        />
    );
};

AddressReadonly.propTypes = {
    address: PropTypes.arrayOf(
        PropTypes.oneOfType([
            PropTypes.string,
            PropTypes.shape({
                name: PropTypes.string,
            }),
        ])
    ).isRequired,
    format: PropTypes.oneOf([
        'oneLine',
        'envelopeBigToSmall',
        'envelopeSmallToBig',
    ]),
    label: intlMessageShape,
    id: PropTypes.string.isRequired,
};
